import React from 'react'
import '../Base/Header.css'
import FormEmailJs from './FormEmailJs';
import LOGO from './assets/LOGO.svg'

function Header() {
    return(
        <div className="header">
            <div className="logoBar">
                 <img className="logo"  src={LOGO} alt="logo falcon"/>
            </div>
            <div className="hero">
                <div>
                    <h1 className="title">Vamos atrair<br/>novos clientes<br/>para o seu negócio!</h1>
                    <h2 className="subTitle">Somos especialistas em estratégias de anúncios pagos no Google, Facebook e Instagram.</h2>
                </div>  
                    <FormEmailJs id="header" />
            </div>
            
        </div>
    )
}

export default Header;