import React from "react";
import emailjs from "emailjs-com";
import { Button, Form } from "react-bootstrap";
import "./forms.css";

export default function ContactUs({ history }) {
  function sendEmail(e) {
    e.preventDefault();

    emailjs
      .sendForm("gmail", "orcamento", e.target, "user_R7PQAQGK4c6FQAXkfFOtP")
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset() 
  }

  return (
    <form onSubmit={sendEmail}>
      <div className="content">
        <div className="input-field">
           <input type="text" className="form" name="name" required="" />
         <label >
           Seu Nome
         </label>
         <span></span>
       </div>
       <div className="input-field">
           <input type="text" className="form" name="email" required="" />
         <label >
           Seu Email
         </label>
         <span></span>
       </div><div className="input-field">
           <input type="text" className="form" name="fone" required="" />
         <label >
           Celular
         </label>
         <span></span>
       </div><div className="input-field">
           <input type="text" className="form" name="busines" required="" />
         <label >
           Nome do seu negócio
         </label>
         <span></span>
       </div>
       <input className="boton" type="submit" value="Enviar"  />
      </div>
       

    </form>
  );
}
